<template>
  <!-- 晋级名单 -->
  <div class="name-list">
    <div class="control-search">
      <search-comp :arr="arr" :searchShow="true"></search-comp>
    </div>
    <div class="text-tit">
      <div class="title">
        <title-comp tit="晋级名单"></title-comp>
      </div>
      <div class="use-scrollX">
        <div class="true-scroll" :style="`width:${scrollWidth}px`">
          <div
            class="scroll-item"
            v-for="(item ,key) in name"
            :key="key"
            :class="{change: item.change}"
            @click="nameClick(key, item.chage)"
          >{{item.name}}</div>
        </div>
      </div>
    </div>
    <div class="content-table">
      <div class="use-padding">
        <ls-table></ls-table>
      </div>
      <div class="use-padding">
        <ls-table></ls-table>
      </div>
    </div>
  </div>
</template>
<script>
import titleComp from '../../../components/titleComp/titleComp'
import layout from '../../../components/layout/layout'
import searchComp from '../../../components/searchFlowPath/searchFlowPath'
import lsTable from '../../../components/table/table'
export default {
  data () {
    return {
      name: [
        { name: '全部', change: false },
        { name: '组别1', change: true },
        { name: '组别2', change: false },
        { name: '组别3', change: false },
        { name: '组别4', change: false }
      ],
      arr: [
        { text: '市初赛' },
        { text: '市复赛' },
        { text: '市决赛' },
        { text: '全国决赛' }
      ],
      list: [
        {
          id: 1,
          name: 'name1',
          school: 'school1',
          phone: '115711513',
          total: '80'
        },
        {
          id: 2,
          name: 'name2',
          school: 'school2',
          phone: '115711513',
          total: '50'
        },
        {
          id: 3,
          name: 'name3',
          school: 'school3',
          phone: '115711513',
          total: '20'
        },
        {
          id: 4,
          name: 'name4',
          school: 'school4',
          phone: '115711513',
          total: '90'
        }
      ]
    }
  },
  components: {
    layout,
    searchComp,
    lsTable,
    titleComp
  },
  computed: {
    scrollWidth () {
      const sum = this.name.length
      return sum * 90
    }
  },
  methods: {
    nameClick (i, change) {
      if (change) return
      this.name.map(item => {
        item.change = false
      })
      this.name[i].change = true
    }
  }
}
</script>
<style lang="less">
.name-list {
  width: 100%;
  box-sizing: border-box;
  padding: 0 18px;
  .control-search {
    // margin-top: 15px;
    padding-top: 15px;
  }
  .text-tit {
    // width: 100%;
    // font-size: 16px;
    // color: #333333;
    // font-weight: bold;
    // margin-top: 12px;
    padding-top: 12px;
    // letter-spacing: 1px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .title {
      width: 80px;
    }
    .use-scrollX {
      overflow: auto;
      width: 80%;
      flex: 1;
      .true-scroll {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        .scroll-item {
          width: 70px;
          padding: 4px 0;
          font-size: 16px;
          color: #999999;
          border-radius: 6px;
          background: #e5e5e5;
          margin-right: 20px;
          text-align: center;
        }
        .change {
          color: #ffffff;
          background: #fe741d;
        }
      }
    }
  }
  .content-table {
    width: 100%;
    // margin-top: 12px;
    padding-top: 12px;
    .use-padding {
      // margin-bottom: 10px;
      padding-bottom: 10px;
    }
  }
}
</style>



